@use "sass:meta";

@layer bs-base, bs-layout, bs-components, bs-utilities;

@layer bs-base {
    @layer bs-root, bs-reboot, bs-type;
}

@layer bs-root {
    @include meta.load-css("root");
}

@layer bs-reboot {
    @include meta.load-css("reboot");
}

@layer bs-type {
    @include meta.load-css("type");
}

@layer bs-layout {
    @layer bs-containers;
}

@layer bs-containers {
    @include meta.load-css("containers");
}

@layer bs-components {
    @layer bs-image;
}

@layer bs-image {
    @include meta.load-css("images");
}
